@import '~src/css/quasar.variables.scss', 'quasar/src/css/variables.sass';
/* Font Styles */
@import url(styles/_fonts.scss);
/* Placeholder style */
@import url(styles/_placeholder.scss);
/* Custom Icon Font */
@import url(styles/_icons-font.scss);
/* Image Styles */
@import url(styles/_images.scss);
/* Custom Classes */
@import url(styles/_classes.scss);
/* Main Drawer */
@import url(styles/_main_drawer.scss);

html {
  font-size: 95%;
}

body {
  letter-spacing: map-get($letter-spacing, 'standard');
}

a {
  text-decoration: none;
  color: $secondary;
  &:hover {
    color: $secondary;
  }
}

p {
  &:last-child {
    margin-bottom: 0;
  }
}

.shadow {
  &-ui {
    box-shadow: $shadow-ui !important;
  }
  &-gm {
    box-shadow: $shadow-gm !important;
  }
}

.notif {
  &-ui {
    margin-bottom: 60px;
    margin-top: -40px;
  }
}

.button-hover {
  &:hover {
    & .q-btn {
      &:not(.q-btn--flat) {
        &:before {
          transition: $shadow-transition;
        }
        &:before {
          box-shadow: $shadow-gm;
        }
        & .q-focus-helper {
          opacity: 0 !important;
        }
      }
    }
  }
}

.q {
  &-btn {
    &:not(.q-btn--flat, .not-gm, .disabled) {
      &:before {
        transition: $shadow-transition;
      }
      &:hover {
        &:before {
          box-shadow: $shadow-gm;
        }
        & .q-focus-helper {
          opacity: 0 !important;
        }
      }
    }
    &--outline {
      &.light-outline {
        &::before {
          border-color: $border-color;
        }
      }
    }
    &:hover {
      &.hover-border {
        &-primary {
          &:before {
            border-color: $primary;
          }
        }
        &-secondry {
          &:before {
            border-color: $secondary;
          }
        }
        &-accent {
          &:before {
            border-color: $accent;
          }
        }
      }
    }
  }

  &-editor {
    &:hover,
    &:active,
    &:focus-visible,
    &:focus-within,
    &:focus-within {
      border-color: $secondary;
    }
    border-color: $border-color;
  }
  &-field {
    &--focused,
    &--highlighted,
    &:hover,
    &:active,
    &:focus-visible,
    &:focus-within,
    &:focus-within {
      & .hover {
        &-bg {
          &-primary {
            background: $primary !important;
          }
          &-secondary {
            background: $secondary !important;
          }
          &-accent {
            background: $accent !important;
          }
        }
        &-text {
          &-primary {
            color: $primary !important;
          }
          &-secondary {
            color: $secondary !important;
          }
          &-accent {
            color: $accent !important;
          }
        }
      }
      & .q-field {
        &__control {
          &:before,
          &:after {
            border-width: map-get($borders, 'normal');
          }
        }
      }
      & .q-separator {
        border-color: $secondary !important;
      }
    }
    & .q-separator {
      transition: $generic-hover-transition;
      width: 0;
      border-left: map-get($borders, 'normal') solid $border-color;
    }
    &--outlined {
      & .q-field {
        &__control {
          &:before,
          &:after {
            border-width: map-get($borders, 'normal') !important;
            border-color: $border-color;
          }
        }
      }
    }
    &.format-bold {
      &.q-field {
        & .q-separator {
          transition: $generic-hover-transition;
          border-left: map-get($borders, 'md') solid $primary;
        }
        &--outlined {
          & .q-field {
            &__control {
              &:before,
              &:after {
                border-width: map-get($borders, 'md') !important;
                border-color: $primary;
              }
            }
          }
        }
      }
      &-light {
        &.q-field {
          & .q-separator {
            transition: $generic-hover-transition;
            border-left: map-get($borders, 'md') solid $border-color;
          }
          &--outlined {
            & .q-field {
              &__control {
                &:before,
                &:after {
                  border-width: map-get($borders, 'md') !important;
                  border-color: $border-color;
                }
              }
            }
          }
        }
      }
    }
    &.table-select-filter {
      & .q-field__native {
        font-weight: $font-medium;
        font-size: map-get($body2, 'size');
      }
    }
    &.q-field {
      &--focused,
      &--highlighted,
      &:hover,
      &:active,
      &:focus-visible,
      &:focus-within,
      &:focus-within {
        & .q-field {
          &__control {
            &:not(.text-negative) {
              &:before,
              &:after {
                border-color: $secondary;
              }
            }
            &.text-negative {
              &:before,
              &:after {
                border-color: $negative;
              }
            }
          }
        }
      }
    }
    &--outlined {
      &:before,
      &:after {
        border-width: map-get($borders, 'normal');
      }
      & .q-field {
        &__control {
          border-radius: $border;
          &:before,
          &:after {
            border-color: $border-color;
          }
        }
      }
    }
    &--dense {
      &:not(.select-request, .small-field) {
        &:not(.q-textarea) {
          & .q-field {
            &__control,
            &__marginal,
            &__native {
              height: 45px;
              min-height: 45px;
            }
          }
        }
        & .q-field {
          &__control,
          &__marginal,
          &__native {
            min-height: 45px;
          }
          &__label {
            top: 8px;
          }
        }
      }
      &.small-field {
        &:not(.q-textarea) {
          & .q-field {
            &__control,
            &__marginal,
            &__native {
              height: 35px;
              min-height: 35px;
            }
          }
        }
        & .q-field {
          &__control,
          &__marginal,
          &__native {
            min-height: 35px;
          }
          &__label {
            top: 8px;
          }
        }
      }
      & .q-field {
        &__bottom {
          font-size: map-get($body2, 'size') !important;
          padding: 8px 0;
        }
      }
    }
    &.q-select {
      & .q-select {
        &__dropdown-icon {
          color: $placeholder-color;
        }
      }
    }
  }

  &-textarea {
    & .q-field__control {
      height: auto;
    }
  }

  &-dialog {
    &__title {
      letter-spacing: map-get($letter-spacing, 'standard');
    }
    &:not(.q-dialog--seamless) {
      background-color: rgba(0, 0, 0, 0.8);
    }
    &.backdrop {
      &-light {
        & .q-dialog__backdrop {
          background-color: rgba(0, 0, 0, 0.4);
        }
      }
      &-blur {
        & .q-dialog__backdrop {
          backdrop-filter: blur(7px);
        }
      }
    }
  }

  &-card {
    &.action {
      &-absolute {
        & .q-card__actions {
          margin: 0;
          padding: 0;
          height: 0;
          min-height: unset;
        }
      }
      &-none {
        & .q-card__actions {
          display: none;
        }
      }
    }
    &--bordered {
      border-color: $border-color;
    }
  }

  &-uploader {
    &.files-uploader {
      & .q-uploader {
        &__header {
          height: inherit;
          background: transparent;
        }
        &__list {
          padding: 0;
          min-height: unset;
        }
      }
      &.with-items {
        max-height: 450px;
      }
      &:not(.with-items) {
        & .q-uploader {
          &__list {
            display: none;
          }
        }
      }
    }
  }

  &-pagination {
    &.rounded {
      & .q-btn-item {
        border-radius: $border;
      }
    }
  }

  &-rating {
    &.no-shadow {
      & .q-rating__icon {
        text-shadow: unset;
      }
    }
  }

  &-radio {
    &.flex-label {
      & .q-radio {
        &__label {
          display: flex;
          align-items: center;
        }
      }
    }
  }

  &-table {
    & .q-table {
      &__progress > th {
        position: sticky;
        z-index: 4;
        top: 1px;
      }
    }
    &__container {
      & tbody {
        & tr {
          &.selected {
            & td {
              &:before,
              &:after {
                background: unset !important;
              }
            }
          }
          & td {
            &:before,
            &:after {
              background: unset !important;
            }
          }
        }
      }
    }
  }

  &-date {
    &__calendar {
      &-item {
        & .q-btn {
          &:before {
            box-shadow: unset !important;
          }
        }
      }
    }
  }
}
