@import '~src/css/quasar.variables.scss', 'quasar/src/css/variables.sass';
@import url(_classes/_index.scss);
@import url(_extended_component.scss);

/* Other Custom Classess */
.round {
  border-radius: 50%;
}

.sticky {
  position: -webkit-sticky;
  position: sticky;
  &-menu {
    &-top {
      top: 25px;
    }
  }
}

.full {
  &-opacity {
    opacity: 1;
  }
}

.disabled {
  &-state {
    color: $grey-4 !important;
    border-color: $grey-3 !important;
    & * {
      color: $grey-8 !important;
      border-color: $grey-3 !important;
    }
  }
}

.target {
  &-clickable {
    &-id {
      &:hover {
        cursor: pointer;
        &.with {
          &-hash {
            &:after {
              content: ' #';
            }
          }
        }
      }
    }
  }
}

.transition {
  transition: $generic-hover-transition;
  &-md {
    transition: map-get($transition-durations, 'md')
      cubic-bezier(0.25, 0.8, 0.5, 1);
  }
  &-lg {
    transition: map-get($transition-durations, 'lg')
      cubic-bezier(0.25, 0.8, 0.5, 1);
  }
  &-xl {
    transition: map-get($transition-durations, 'xl')
      cubic-bezier(0.25, 0.8, 0.5, 1);
  }
}

.auto {
  &-h {
    height: auto;
  }
  &-w {
    width: auto;
  }
  &-hw {
    height: auto;
    width: auto;
  }
}

.letter {
  &-space {
    &-standard {
      letter-spacing: map-get($letter-spacing, 'standard');
    }
    &-normal {
      letter-spacing: map-get($letter-spacing, 'normal');
    }
    &-sm {
      letter-spacing: map-get($letter-spacing, 'sm');
    }
    &-md {
      letter-spacing: map-get($letter-spacing, 'md');
    }
    &-lg {
      letter-spacing: map-get($letter-spacing, 'lg');
    }
  }
}

.small {
  &-innersearch {
    & input {
      max-width: 5px !important;
    }
  }
  &-title {
    line-height: 0rem;
  }
}

.no {
  &-min {
    &-height {
      min-height: unset;
    }
    &-width {
      min-width: unset;
    }
  }
  &-hover {
    &-helper {
      &:hover {
        & .q-focus-helper {
          opacity: 0 !important;
        }
      }
    }
  }
  &-ellipsis {
    white-space: normal;
    text-overflow: unset;
    overflow: unset;
  }
  &-border {
    border: 0 !important;
    & .q-field__control {
      border: 0 !important;
      &::before,
      &::after {
        border: 0 !important;
      }
    }
  }
}

.dnav {
  .dicon {
    transition-delay: 0.4s;
    color: $grey-4;

    &.active,
    &.past {
      color: $secondary;
    }
  }

  .dline {
    height: 3px;
    width: 100px;
    max-width: 100%;
    background-color: $grey-4;

    background: linear-gradient(to left, $grey-4 50%, $secondary 50%) right;
    background-size: 200%;
    transition: 0.5s ease-out;
    &-mini {
      width: 70px;
      &-xs {
        width: 10px !important;
      }
    }
    &.active,
    &.past {
      background-position: left;
    }
  }
}

.field {
  &-select {
    &.format-bold {
      border-width: map-get($borders, 'md');
      border-color: $primary;
      &:hover,
      &:active,
      &:focus,
      &:focus-visible,
      &[aria-checked='true'] {
        border-width: map-get($borders, 'md');
      }
    }
    border: map-get($borders, 'normal') solid $border-color;
    &:not(.disabled),
    &:not([aria-disabled='true']) {
      &:hover,
      &:active,
      &:focus,
      &:focus-visible,
      &[aria-checked='true'] {
        border-width: map-get($borders, 'normal');
        border-color: $secondary;
        color: $secondary;
        background-color: $accent;
      }
    }
    &-custom {
      & .q-radio__inner,
      & .q-checkbox__inner {
        height: auto;
        &:before {
          display: none !important;
        }
      }
    }
    &-none {
      & .q-radio__inner,
      & .q-checkbox__inner {
        display: none;
      }
    }
  }
}
