@import '~src/css/quasar.variables.scss', 'quasar/src/css/variables.sass';
.bg {
  &-translucent {
    background: rgba(white, $translucent);
    &-dark {
      background: rgba(grey, $translucent);
    }
    &-light {
      background: rgba(white, $translucent);
    }
  }
  &-none {
    background: transparent !important;
    &:hover {
      background: transparent !important;
      & .q-focus-helper {
        background-color: transparent !important;
        &:before,
        &:after {
          background-color: transparent !important;
        }
      }
    }
  }
  &-accent2 {
    background-color: $accent2;
    & .q-focus-helper {
      background-color: $accent2;
    }
  }
  &-color {
    background-color: $accent2 !important;
  }
  &-gradient {
    background-image: linear-gradient(transparent, white);
    &-color {
      background-image: linear-gradient(transparent, $accent2);
    }
    &-right {
      background-image: linear-gradient(to right, transparent, white);
      &-color {
        background-image: linear-gradient(to right, transparent, $accent2);
      }
    }
    &-left {
      background-image: linear-gradient(to left, transparent, white);
      &-color {
        background-image: linear-gradient(to left, transparent, $accent2);
      }
    }
  }
  &-gmc {
    &-green {
      background-color: $gmc-green;
    }
  }
}
