@import '~src/css/quasar.variables.scss', 'quasar/src/css/variables.sass';
.border {
  &-add {
    border: map-get($borders, 'normal') solid $border-color;
  }
  &-secondary {
    border-color: $secondary;
  }
  &-primary {
    border-color: $primary;
  }
  &-color {
    border-color: $border-color;
  }
  &-md {
    border-width: map-get($borders, 'md');
  }
  &-lg {
    border-width: map-get($borders, 'lg');
  }
  &-xl {
    border-width: map-get($borders, 'xl');
  }
  &-dashed {
    border-style: none;
    background-repeat: no-repeat;
    background-image: url("data:image/svg+xml,%3csvg width='100%25' height='100%25' xmlns='http://www.w3.org/2000/svg'%3e%3crect width='100%25' height='100%25' fill='none' rx='8' ry='8' stroke='#{str-replace(#{$border-color}, '#', '%23')}' stroke-width='#{map-get($borders, 'md')}' stroke-dasharray='10%2c 10' stroke-dashoffset='21' stroke-linecap='round'/%3e%3c/svg%3e");
  }
  &-solid {
    border-style: solid;
  }
}

.rounded {
  border-radius: $border !important;
  &-top {
    border-top-left-radius: $border;
    border-top-right-radius: $border;
  }
  &-bottom {
    border-bottom-left-radius: $border;
    border-bottom-right-radius: $border;
  }
  &-left {
    border-top-left-radius: $border;
    border-bottom-left-radius: $border;
  }
  &-right {
    border-top-right-radius: $border;
    border-bottom-right-radius: $border;
  }
}
