@import '~src/css/quasar.variables.scss', 'quasar/src/css/variables.sass';
$card-height: 150px;
$card-height-mini: calc(150px / 2 - 8px);

.card {
  &-quicklinks {
    width: 100%;
    height: auto;
    max-height: 100%;
    max-width: 100%;
    cursor: pointer;
    overflow: hidden;
    border-width: map-get($borders, 'normal');
    border-style: solid;
    border-color: $border-color;
    border-radius: $border;
    &:not(.no-min-height) {
      min-height: $card-height;
      &.card-quicklinks-new {
        min-height: $card-height;
      }
      &.card-quicklinks-mini {
        min-height: $card-height-mini;
      }
      &.brand {
        min-height: $card-height;
      }
    }

    & .q-separator {
      height: 1px;
      line-height: 1rem;
      margin: 0;
      padding: 0;
      border: 0;
    }

    &.brand {
      & .brand-title {
        max-height: 88px;
        overflow-y: hidden;
        text-overflow: ellipsis;
      }
      max-height: 100%;
    }

    &-mini {
      max-height: 100%;
    }
    & .card-bg-circle {
      transition: transform 0.5s;
      color: rgba($accent, 0.25);
      display: inline;
      position: absolute;
      right: -35px;
      top: -35px;
    }
    &:hover {
      z-index: 3;
      & .hover-bg-accent {
        background-color: $accent !important;
      }
      & .hover-text-secondary {
        color: $secondary !important;
        .q-icon {
          color: $secondary !important;
        }
      }
      & .card-icon {
        color: $secondary !important;
        .q-icon {
          color: $secondary !important;
        }
      }
      & .card-bg-circle {
        color: $accent;
        transform: scale(1.3);
      }
    }
  }
  &-bordered {
    border-width: map-get($borders, 'normal');
    border-style: solid;
    border-color: $border-color;
    &-dashed {
      border-style: dashed;
    }
  }
}
