@import '~src/css/quasar.variables.scss', 'quasar/src/css/variables.sass';
/* Placeholder color */
.text-placeholder {
  color: $placeholder-color;
}

::placeholder {
  /* Chrome, Firefox, Opera, Safari 10.1+ */
  color: $placeholder-color !important;
}

:-ms-input-placeholder {
  /* Internet Explorer 10-11 */
  color: $placeholder-color !important;
}

::-ms-input-placeholder {
  /* Microsoft Edge */
  color: $placeholder-color !important;
}
