@import '~src/css/quasar.variables.scss', 'quasar/src/css/variables.sass';
.q {
  &-stepper {
    &.max-640 {
      width: 640px;
      max-width: 640px;
      & .q-stepper {
        &__step-inner {
          width: 640px;
        }
        &__dot {
          font-size: map-get($subtitle1, 'size');
          font-weight: $font-medium;
        }
      }
    }

    &.small {
      & .q-stepper {
        &__title {
          font-size: map-get($subtitle1, 'size');
        }
        &__dot {
          width: 26px;
          min-width: 26px;
          height: 26px;
          & span {
            font-size: map-get($body1, 'size');
          }
        }
      }
    }

    &:not(.small) {
      & .q-stepper {
        &__title {
          font-size: map-get($subtitle1, 'size');
        }
        &__dot {
          width: 40px;
          min-width: 40px;
          height: 40px;
          & span {
            font-size: map-get($subtitle1, 'size');
          }
        }
      }
    }

    &__title {
      font-weight: $font-normal;
      letter-spacing: map-get($letter-spacing, 'normal');
    }

    &__tab {
      &--active {
        & .q-stepper {
          &__title {
            font-weight: $font-medium;
          }
        }
      }
    }

    &.new-brand-stepper {
      & .q-stepper {
        &__tab {
          &:first-child,
          &:last-child {
            padding-left: 0 !important;
            padding-right: 0 !important;
          }
        }
      }
    }

    &.title-only {
      & .q-stepper {
        &__title {
          font-size: map-get($body2, 'size');
        }
        &__step {
          &-content {
            display: none;
          }
        }
        &__dot {
          background-color: transparent;
          &:before,
          &:after {
            background-color: white;
          }
          & .q-icon {
            font-size: 26px;
          }
        }
      }
      &.rounded {
        & .q-stepper {
          &__tab {
            border-radius: $border;
          }
        }
      }
    }
    &__step {
      &-inner {
        padding-top: 0 !important;
        padding-bottom: 0 !important;
      }
      &.full {
        &-width {
          & .q-stepper {
            &__step {
              &-content {
                padding: 0;
                width: 100% !important;
                margin-left: 0 !important;
                margin-right: 0 !important;
              }
            }
          }
        }
        &-inner {
          & .q-stepper {
            &__step {
              &-inner {
                padding: 12px;
                width: 100% !important;
                margin-left: 0 !important;
                margin-right: 0 !important;
              }
            }
          }
        }
      }
    }
    & .tab-no-padding {
      & .q-stepper {
        &__tab {
          padding-left: 0;
          padding-right: 0;
        }
      }
    }
  }
}

@media (max-width: $breakpoint-md-max) {
  .q {
    &-stepper {
      &.max-640 {
        & .q-stepper__step-inner {
          width: auto;
        }
      }
    }
  }
}
